
import Vue from 'vue'

export default Vue.extend({
  name: 'ImageUpload',
  data() {
    return {
      file: null,
    }
  },
  watch: {
    file(newFile) {
      this.emitImage(newFile)
    },
  },
  methods: {
    emitImage(imageFile: File) {
      this.$emit('image-uploaded', imageFile)
    },
    windowSizeNotPhone() {
      return window.innerWidth > 800
    },
  },
})
